<template>
    <l-panel>
        <form @submit.prevent="onSubmit" class="form">
            <div v-for="(field, index) of fields" :key="index">
                <l-field
                    horizontal
                    :name="field.name"
                    :label="field.label"
                    :error="errors[field.name]"
                >
                    <component
                        v-model="form[field.name]"
                        :is="`l-${field.component}`"
                        :name="field.name"
                        :options="field.options"
                        :hasError="errors[field.name] != undefined"
                        :placeholder="field.placeholder"
                    />
                </l-field>
            </div>
            <div class="form-footer">
                <l-btn type="submit" variant="primary">
                    Submit
                </l-btn>
            </div>
        </form>
    </l-panel>
</template>

<script>
import { useForm } from '@inertiajs/inertia-vue3'

export default {
    props: {
        data: {
            type: Object
        },
        route: {
            type: String,
            required: true
        },
        errors: {
            type: Object,
            required: true
        },
        fields: {
            type: Object,
            required: true
        }
    },
    data () {
        let form = this.data || {}

        if (! this.data) {
            const columns = Object.keys(this.fields)

            for (const column of columns) {
                form[column] = null
            }
        }

        return {
            form: useForm(form)
        }
    },
    methods: {
        onSubmit () {
            const method = this.form.id ? 'put' : 'post'

            this.form.submit(method, this.route, {})
            this.$emit('submited')
        }
    }
}
</script>
